html
{
  height: 100vh;
}

body,
#root
{
  height: 100%;
}

.App {
  padding-top: 40px;
  height: calc(100vh - 40px)
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.Header {
  background-color: #222;
  height: 40px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  color: white;
  padding: 0 100px;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.App-title {
  font-size: 18px;
  font-weight: normal;
  margin: 0;
  line-height: 40px;
}

.Menu
{
  position: fixed;
  right: -400px;
  top: 40px;
  width: 300px;
  height: calc(100vh - 80px);
  background: rgba(255, 255, 255, 0.9);
  z-index: 100;
  padding: 20px;
  -webkit-transition: right 0.1s ease-in-out;
  -o-transition: right 0.1s ease-in-out;
  transition: right 0.1s ease-in-out;
}

.Menu-open
{
  right: 0;
}

.Menu-indexControl
{
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  height: 200px;
  resize: none;
}

.Menu-label
{
  margin-bottom: 12px;
  font-size: 14px;
}

.Header-btn
{
  border: none;
  background: none;
  color: white;
}

.Header-btn:hover
{
  cursor: pointer;
}

.Header-btn:focus
{
  outline: none;
}

.Hexagon:hover
{
  cursor: pointer;
}

.Inspector
{
  background: #0000004f;
  position: absolute;
  padding: 7px 14px;
  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
}



